.homepage {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.home-input-container {
	width: 100%;
	height: 10px;
	display: flex;
	place-content: center;
	padding: 15px 0px;
}

.home-input {
	width: 60%;
	height: 25px;
}
.home-search-section {
	width: 100%;
}

.home-body {
	width: 60%;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px, 3fr));
	align-self: center;
	justify-content: center;
	gap: 1rem;
	margin-top: 20px;
}
